import {
	getBannerList,
	apiGetRecommendSong,
	apiGetTopAlbum,
	apiGetRelatedVideo
} from "../../common/js/index.js"
import songList from '../../components/songList.vue'
export default {
	components: {
		songList
	},
	data() {
		return {
			swiperList: [],
			contenrBar: [{
					name: "每日推荐"
				},
				{
					name: "歌单"
				},
				{
					name: "排行榜"
				},
				{
					name: "电台"
				},
				{
					name: "直播"
				},
			],
			recommendSong: [], //推荐歌单
			newType: 1,
			latestAlbum: [], //新碟新歌显示的
			latestTempAlbum: [], //全部新碟新歌
			relatedVideo: [] //精选视频

		}
	},
	onLoad() {
		this.getBanner()
		this.getRecommendSong()
		this.getLatestAlbum()
		this.getRelatedVideo()
	},
	onPullDownRefresh() {
		setTimeout(() => {
			uni.stopPullDownRefresh()
		}, 1000)
	},
	methods: {
		getBanner() {
			getBannerList().then(data => {
				console.log('data', data)
				this.swiperList = data.banners
			})
		},
		// 推荐歌单 
		getRecommendSong() {
			apiGetRecommendSong({
				limit: 6
			}).then(data => {
				const formatCount = item => {
					let tmp = item
					if (item > 10000) {
						tmp = (parseInt(item / 10000) + '万')
					}
					return tmp
				}

				this.recommendSong = data.result

				this.recommendSong.forEach(item => {
					item.playCount = formatCount(item.playCount)
				})
				// console.log('recommendSong',this.recommendSong)

			})
		},
		// 新碟新歌 
		getLatestAlbum() {
			apiGetTopAlbum().then(data => {
				console.log('data', data)
				// 所有的数据存储在latestTempAlbum
				this.latestTempAlbum = data.albums
				this.latestAlbum = data.albums.slice(0, 3)
			})
		},
		// 切换新碟新歌
		switchTab(tag) {
			this.newType = tag
			let temp = {
				s: tag == 1 ? 0 : 3,
				e: tag == 1 ? 3 : 6
			}
			this.latestAlbum = this.latestTempAlbum.slice(temp.s, temp.e)
		},
		// 精选视频
		getRelatedVideo() {
			apiGetRelatedVideo({
				id: 32154
			}).then(data => {
				console.log('data', data.data)
				this.relatedVideo = data.data
			})
		}

	},
}
